import PictureList from '../components/PictureList'
import VovDragger from '../components/VovDragger'
import castArray from 'lodash/castArray'
import Snag from '../components/Snag'
import token from '../utils/token'
import ReactDOM from 'react-dom'
import '../styles/vov.scss'
import React from 'react'

export default class Vov extends React.Component {
  state = {
    pictures: []
  }

  setPictures = pictures => {
    this.setState({ pictures: castArray(pictures).map(url => ({ url, token: token.token })) })
  }

  render () {
    const { state: { pictures }, setPictures } = this
    return (
      <section className="vov">
        {pictures.length
          ? <PictureList pictures={pictures} />
            : <VovDragger></VovDragger>}
        <Snag setPictures={setPictures}></Snag>
      </section>
    )
  }
}



