import '../styles/picture-list.scss'
import { message, Icon } from 'antd'
import PropTypes from 'prop-types'
import copy from '../utils/copy'
import ReactDOM from 'react-dom'
import React from 'react'

export default class PictureList extends React.Component {
  static propTypes = {
    pictures: PropTypes.oneOfType([PropTypes.object, PropTypes.array]),
    remove: PropTypes.bool,
    grid: PropTypes.bool
  }

  removePictureByKey = token => {
    this.props.onRemovePicture(token)
  }

  render () {
    const { props: { pictures, grid, children, remove }, removePictureByKey } = this
    return (
      <section className={`picture-list ${grid ? 'grid' : ''}`}>
        {pictures.map(({ url, token }) => {
          return (
            <div key={token}>
              {remove && <Icon onClick={removePictureByKey.bind(this, token)} type="delete" />}
              <img onClick={copy.bind(this, url)} src={url} alt="" />
              {children && children(url) }
            </div>
          )
        })}
      </section>
    )
  }
}



